.Login {
    padding: 0;

}

.Login Form {
    margin: 0 auto 25px auto;
    max-width: 80%;
    max-height: 100%;
    padding: 15px 30px;
    border-style: solid;
    border-radius: 15px;
    border-width: thin;
    background-color: #c4c4bc;
}


.loginBackground {
    background: url(images/loginBackground.jpg) no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

#centerSpace {
    display: grid;
    width: 100vw;
    height: 100vh;
    /* width: 100%; */
    /* height: 100%; */
}

#info {
    margin: 15px auto;
    max-width: 80%;
    padding: 30px 30px;
    border-style: solid;
    border-radius: 15px;
    border-width: thin;
    background-color: #c4c4bc;

    max-height: 100%;
}

h2 {
    font-size: 40px;
}

#SignIn {
    width: 100%;
    background-color: #f5c24c;
}

#CreateAccount {
    width: 40%;
    background-color: #6ac9e6;
    margin-right: 5%;
}

#ForgotCredentials {
    width: 55%;
    background-color: #6ac9e6;

}

#SideButtons {
    position: relative;
}